<template>
  <div class="table">
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          size="mini"
          class="handle-del mr10"
          @click="delAll"
          >Batch Delete</el-button
        >
        <el-input
          v-model="select_word"
          size="mini"
          placeholder="Select user"
          class="handle-input mr10"
        ></el-input>
        <el-button
          type="primary"
          size="mini"
          @click="centerDialogVisible = true"
          >+ new user</el-button
        >
      </div>
      <el-table
        :data="data"
        border
        size="mini"
        style="width: 100%"
        ref="multipleTable"
        height="550px"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="40"
          align="center"
        ></el-table-column>
        <el-table-column label="Singer image" width="102" align="center">
          <template slot-scope="scope">
            <img :src="getUrl(scope.row.avator)" alt="" style="width: 80px;" />
            <el-upload
              class="upload-demo"
              :action="uploadUrl(scope.row.id)"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <el-button size="mini">Update image</el-button>
            </el-upload>
          </template>
        </el-table-column>
        <el-table-column
          label="UserName"
          prop="username"
          width="80"
          align="center"
        ></el-table-column>
        <el-table-column
          label="Password"
          prop="password"
          width="80"
          align="center"
        ></el-table-column>
        <el-table-column label="Gender" width="50" align="center">
          <template slot-scope="scope">
            <div>{{ changeSex(scope.row.sex) }}</div>
          </template>
        </el-table-column>
        <el-table-column
          label="Phone"
          prop="phoneNum"
          width="120"
          align="center"
        ></el-table-column>
        <el-table-column
          label="Email"
          prop="email"
          width="120"
          align="center"
        ></el-table-column>
        <el-table-column label="Birthday" width="120" align="center">
          <template slot-scope="scope">
            <div>{{ attachBirth(scope.row.birth) }}</div>
          </template>
        </el-table-column>
        <el-table-column
          prop="introduction"
          label="Signature"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="location"
          label="Region"
          width="80"
          align="center"
        ></el-table-column>
        <el-table-column label="Collections" width="80" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="getCollect(data[scope.$index].id)"
              >Collections</el-button
            >
          </template>
        </el-table-column>
        <el-table-column label="Operation" width="150" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.row)"
              >Modify</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row.id)"
              >Delete</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          @current-change="handleCurrentChange"
          background
          layout="total, prev, pager, next"
          :current-page="currentPage"
          :page-size="pageSize"
          :total="tableData.length"
        >
        </el-pagination>
      </div>
    </div>

    <el-dialog
      title="Add user"
      :visible.sync="centerDialogVisible"
      width="600px"
      center
    >
      <el-form
        :model="registerForm"
        status-icon
        :rules="rules"
        ref="registerForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="UserName" prop="username" size="mini">
          <el-input
            v-model="registerForm.username"
            placeholder="UserName"
          ></el-input>
        </el-form-item>
        <el-form-item label="password" prop="password" size="mini">
          <el-input
            type="password"
            placeholder="password"
            v-model="registerForm.password"
          ></el-input>
        </el-form-item>
        <el-form-item label="Gender" size="mini">
          <el-radio-group v-model="registerForm.sex">
            <el-radio :label="0">Female</el-radio>
            <el-radio :label="1">Male</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="Phone" prop="phoneNum" size="mini">
          <el-input
            placeholder="Phone"
            v-model="registerForm.phoneNum"
          ></el-input>
        </el-form-item>
        <el-form-item label="Email" prop="email" size="mini">
          <el-input v-model="registerForm.email" placeholder="Email"></el-input>
        </el-form-item>
        <el-form-item label="Birthday" prop="birth" size="mini">
          <el-date-picker
            type="date"
            placeholder="Select a date"
            v-model="registerForm.birth"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="Signature" prop="introduction" size="mini">
          <el-input
            type="textarea"
            placeholder="Signature"
            v-model="registerForm.introduction"
          ></el-input>
        </el-form-item>
        <el-form-item label="Region" prop="location" size="mini">
          <el-select v-model="registerForm.location" placeholder="Region">
            <el-option
              v-for="item in cities"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="centerDialogVisible = false"
          >Cancel</el-button
        >
        <el-button type="primary" size="mini" @click="addPeople">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Modify" :visible.sync="editVisible" width="600px">
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="UserName" size="mini">
          <el-input v-model="form.username" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="password" size="mini">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="Gender" size="mini">
          <el-radio-group v-model="form.sex">
            <el-radio :label="0">Female</el-radio>
            <el-radio :label="1">Male</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="Phone" size="mini">
          <el-input v-model="form.phoneNum"></el-input>
        </el-form-item>
        <el-form-item label="Email" size="mini">
          <el-input v-model="form.email"></el-input>
        </el-form-item>
        <el-form-item label="Birthday" prop="birth" size="mini">
          <el-date-picker
            type="date"
            placeholder="Select a date"
            v-model="form.birth"
            style="width: 100%;"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="Signature" size="mini">
          <el-input v-model="form.introduction"></el-input>
        </el-form-item>
        <el-form-item label="Region" size="mini">
          <el-input v-model="form.location"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="editVisible = false">Cancel</el-button>
        <el-button type="primary" size="mini" @click="saveEdit">OK</el-button>
      </span>
    </el-dialog>

    <el-dialog title="Hint" :visible.sync="delVisible" width="300px" center>
      <div class="del-dialog-cnt" align="center">
        Deletion cannot be recovered. Are you sure to delete it?
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="delVisible = false">Cancel</el-button>
        <el-button size="mini" type="primary" @click="deleteRow"
          >Delete</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mixin } from "../mixins";
import { HttpManager } from "../api/index";

export default {
  name: "consumer-page",
  mixins: [mixin],
  data() {
    return {
      registerForm: {
        username: "",
        password: "",
        sex: "",
        phoneNum: "",
        email: "",
        birth: "",
        introduction: "",
        location: ""
      },
      cities: [
        {
          value: "北京",
          label: "北京"
        },
        {
          value: "天津",
          label: "天津"
        },
        {
          value: "河北",
          label: "河北"
        },
        {
          value: "山西",
          label: "山西"
        },
        {
          value: "内蒙古",
          label: "内蒙古"
        },
        {
          value: "辽宁",
          label: "辽宁"
        },
        {
          value: "吉林",
          label: "吉林"
        },
        {
          value: "黑龙江",
          label: "黑龙江"
        },
        {
          value: "上海",
          label: "上海"
        },
        {
          value: "江苏",
          label: "江苏"
        },
        {
          value: "浙江",
          label: "浙江"
        },
        {
          value: "安徽",
          label: "安徽"
        },
        {
          value: "福建",
          label: "福建"
        },
        {
          value: "江西",
          label: "江西"
        },
        {
          value: "山东",
          label: "山东"
        },
        {
          value: "河南",
          label: "河南"
        },
        {
          value: "湖北",
          label: "湖北"
        },
        {
          value: "湖南",
          label: "湖南"
        },
        {
          value: "广东",
          label: "广东"
        },
        {
          value: "广西",
          label: "广西"
        },
        {
          value: "海南",
          label: "海南"
        },
        {
          value: "重庆",
          label: "重庆"
        },
        {
          value: "四川",
          label: "四川"
        },
        {
          value: "贵州",
          label: "贵州"
        },
        {
          value: "云南",
          label: "云南"
        },
        {
          value: "西藏",
          label: "西藏"
        },
        {
          value: "陕西",
          label: "陕西"
        },
        {
          value: "甘肃",
          label: "甘肃"
        },
        {
          value: "青海",
          label: "青海"
        },
        {
          value: "宁夏",
          label: "宁夏"
        },
        {
          value: "新疆",
          label: "新疆"
        },
        {
          value: "香港",
          label: "香港"
        },
        {
          value: "澳门",
          label: "澳门"
        },
        {
          value: "台湾",
          label: "台湾"
        }
      ],
      rules: {
        username: [{ required: true, trigger: "blur" }],
        password: [{ required: true, trigger: "blur" }],
        sex: [{ required: true, message: "Select gender", trigger: "change" }],
        phoneNum: [{ essage: "请Select a date", trigger: "blur" }],
        email: [
          { message: "Please fill in email address", trigger: "blur" },
          {
            type: "email",
            message: "Please fill in a valid email",
            trigger: ["blur", "change"]
          }
        ],
        birth: [
          {
            required: true,
            type: "date",
            message: "请Select a date",
            trigger: "change"
          }
        ],
        introduction: [
          { message: "Please fill in introduction", trigger: "blur" }
        ],
        location: [{ message: "Please fill in location", trigger: "change" }]
      },
      tableData: [],
      tempDate: [],
      is_search: false,
      multipleSelection: [],
      centerDialogVisible: false,
      editVisible: false,
      delVisible: false,
      select_word: "",
      form: {
        id: "",
        username: "",
        password: "",
        sex: "",
        phoneNum: "",
        email: "",
        birth: "",
        introduction: "",
        location: "",
        createTime: "",
        updateTime: ""
      },
      pageSize: 5,
      currentPage: 1,
      idx: -1
    };
  },
  computed: {
    data() {
      return this.tableData.slice(
        (this.currentPage - 1) * this.pageSize,
        this.currentPage * this.pageSize
      );
    }
  },
  watch: {
    select_word: function() {
      if (this.select_word === "") {
        this.tableData = this.tempDate;
      } else {
        this.tableData = [];
        for (let item of this.tempDate) {
          if (item.username.includes(this.select_word)) {
            this.tableData.push(item);
          }
        }
      }
    }
  },
  created() {
    this.getData();
  },
  methods: {
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    uploadUrl(id) {
      return `${this.$store.state.HOST}/user/avatar/update?id=${id}`;
    },
    getData() {
      this.tableData = [];
      this.tempDate = [];
      HttpManager.getAllUser().then(res => {
        this.tableData = res;
        this.tempDate = res;
        this.currentPage = 1;
      });
    },
    getCollect(id) {
      this.$router.push({ path: "/collect", query: { id } });
    },
    addPeople() {
      let d = this.registerForm.birth;
      let datetime =
        d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let params = new URLSearchParams();
      params.append("username", this.registerForm.username);
      params.append("password", this.registerForm.password);
      params.append("sex", this.registerForm.sex);
      params.append("phone_num", this.registerForm.phoneNum);
      params.append("email", this.registerForm.email);
      params.append("birth", datetime);
      params.append("introduction", this.registerForm.introduction);
      params.append("location", this.registerForm.location);
      params.append("avator", "/img/user.jpg");
      HttpManager.setUser(params)
        .then(res => {
          if (res.code === 1) {
            this.getData();
            this.registerForm = {};
            this.notify("Successfully added", "success");
          } else {
            this.notify("Failed to add ", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.centerDialogVisible = false;
    },
    // Modify
    handleEdit(row) {
      this.idx = row.id;
      this.form = {
        id: row.id,
        username: row.username,
        password: row.password,
        sex: row.sex,
        phoneNum: row.phoneNum,
        email: row.email,
        birth: row.birth,
        introduction: row.introduction,
        location: row.location,
        avator: row.avator
      };
      this.editVisible = true;
    },
    saveEdit() {
      let d = new Date(this.form.birth);
      let datetime =
        d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();
      let params = new URLSearchParams();
      params.append("id", this.form.id);
      params.append("username", this.form.username);
      params.append("password", this.form.password);
      params.append("sex", this.form.sex);
      params.append("phone_num", this.form.phoneNum);
      params.append("email", this.form.email);
      params.append("birth", datetime);
      params.append("introduction", this.form.introduction);
      params.append("location", this.form.location);
      HttpManager.updateUserMsg(params)
        .then(res => {
          if (res.code === 1) {
            this.getData();
            this.notify("successfully modified", "success");
          } else {
            this.notify("Failed to modify", "error");
          }
        })
        .catch(err => {
          console.log(err);
        });
      this.editVisible = false;
    },
    deleteRow() {
      HttpManager.deleteUser(this.idx)
        .then(res => {
          if (res) {
            this.getData();
            this.notify("Deleted successfully", "success");
          } else {
            this.notify("Failed to delete", "error");
          }
        })
        .catch(failResponse => {});
      this.delVisible = false;
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
  font-size: 12px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.pagination {
  display: flex;
  justify-content: center;
}
</style>



